import { CALIBRATION_REQUEST, CALIBRATION_ERROR, CALIBRATION_SUCCESS } from '../actions/calibration'
import axios from 'axios';

const state = { calibration: [] }

const getters = {
  calibration: state => state.calibration,
}

const actions = {
  [CALIBRATION_REQUEST]: ({commit, dispatch}) => {
    return new Promise((resolve, reject) => {
      axios
      .get('/softwarecamera/1.0.0/calibration')
      .then(resp => {
        commit(CALIBRATION_SUCCESS, resp)
        resolve(resp)
      })
      .catch(error => {
        dispatch(CALIBRATION_ERROR, error)
        reject(error)
      });
    })
  },
  changeDirection (state, payload) {
    return new Promise((resolve, reject) => {
      // eslint-disable-next-line
      console.log(payload)
      axios
      .post('/softwarecamera/1.0.0/calibration', payload)
      .then( resp => {
        resolve(resp)
      })
      .catch(error => {
        reject(error)
      });
    })
  },
}

const mutations = {
  [CALIBRATION_SUCCESS]: (state, resp) => {
    state.calibration = resp.data;
  },
  [CALIBRATION_ERROR]: (error) => {
    if (error.response.status === 401) {
      this.$router.push('/login')
    }
    // eslint-disable-next-line
    console.log(error);
  },
}

export default {
  state,
  getters,
  actions,
  mutations,
}
