<template>
  <div>
    <h1>{{$lang.messages.setup_events_header}}</h1>
    <p>{{$lang.messages.setup_events_body}}</p>
    <div class="view-grid" v-for="event in setupevents" v-bind:key="event">
          <label class="no-space-break" v-bind:for="event.Event">
            <input v-bind:id="event.Event" type="checkbox" v-model="event.Enable" @change="changeParamEvents"/>
          {{nameEvent(event.Event)}}</label>
          <input type="color" v-model="event.Color" @change="changeParamEvents"/>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex';

export default {
  name: 'SetupEvents', //this is the name of the component
  data: function () {
    return {
      checkedDetect: false,
      checkedWhitelist: true,
      checkedBlacklist: false,
      colorDetect: '',
      colorWhitelist: '',
      colorBlacklist: '',
    }
  },
  computed:  {
    ...mapGetters([
      'setupevents',
    ]),
    
  },
  
  created() {
    this.$store.dispatch('SETUPEVENTS_REQUEST').then(() => {
      let items = this.setupevents;
      for (var item of items) {
        switch (item.Event) {
          case 'detect':
          this.colorDetect = item.Color;
          this.checkedDetect = (item.Enable == 1);
          break;
          case 'whitelist':
          this.colorWhitelist = item.Color;
          this.checkedWhitelist = (item.Enable == 1);
          break;
          case 'blacklist':
          this.colorBlacklist = item.Color;
          this.checkedBlacklist = (item.Enable == 1);
          break;
        }
      }
    });
  },

  methods: {
    ...mapMutations([
      'changeEvent'
    ]),
    changeParamEvents() {
      var payload = [];
      for (var event of this.setupevents) {
        event.Enable = +event.Enable;
        payload.push(event);
      }
      
      this.$store.dispatch('changeEvent', payload)
      .catch( () => {
        alert('Not saved data')
      });
    },
    nameEvent(e) {
      switch ( e.toString().toLowerCase() ) {
        case 'detect':
          return this.$lang.messages.setup_events_detect;
        case 'whitelist':
          return this.$lang.messages.setup_events_whitelist;
        case 'blacklist':
          return this.$lang.messages.setup_events_blacklist;
      }
      return e;
    },
  },
}
</script>
<style scoped>
.view-grid {
  width: 30%;
  text-align: left;
  display: grid;
  grid-gap: 12px;
  grid-template-columns: repeat(2, 1fr);
  grid-auto-columns: auto
}

.no-space-break {
  white-space:nowrap;
}

</style>