<template>
  <div class="page">
    <div class="header-title">
        <h1 class="header-message">{{$lang.messages.header}} {{$lang.messages.header_description}}</h1>
        <div class="change-lang"><button v-on:click="changeLang($event)">{{lang_l}}</button></div>
    </div>
    
    <div  class="content">
      <nav class="main-menu">
        <router-link
          class="padding-bottom=15px;"
          v-for="routes in links" 
          v-bind:key="routes.id"
          :to="`${routes.page}`">{{routes.text}}</router-link>
      </nav>
      <router-view  class="text-info"/>
    </div>
    <div class="footer">
      <p>RVi group 2018(C)</p>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Navigation',
  data: function() {
    return {
      lang_l: '',
      
    }
  },
  computed: {
    links: function(){
      var idx = 0;
      return [
        {
          id: idx++,
          text: this.$lang.messages.info,
          page:'/info'
        },
        {
          id: idx++,
          text: this.$lang.messages.auth_change,
          page: '/auth',
          component: 'ChangeAuth'
        },
        {
          id: idx++,
          text: this.$lang.messages.detectors,
          page: '/detectors',
          component: 'Detectors'
        },
        {
          id: idx++,
          text: this.$lang.messages.numberplates,
          page: '/numberplates',
          component: 'NumberPlates'
        },
        {
          id: idx++,
          text: this.$lang.messages.setup_events,
          page: '/setupevents',
          component: 'SetupEvents'
        },
        {
          id: idx++,
          text: this.$lang.messages.list_events,
          page: '/listevents',
          component: 'ListEvents'
        },
        {
          id: idx++,
          text: this.$lang.messages.calibration,
          page: '/calibration'
        },
        {
          id: idx++,
          text: this.$lang.messages.onvif,
          page: '/onvif',
          component: 'Onvif'
        },
      ]
    }
  },
  mounted() {
    this.$lang.setLang('ru')
  },
  created() {
    //this.$lang.setLang('ru')
    this.$lang.setLang('ru')
    this.lang_l = this.$lang.getLang();
  },
  methods: {
    changeLang: function(e) {
      if ( this.lang_l === 'ru') {
        this.lang_l = 'en';
      } else if ( this.lang_l === 'en') {
        this.lang_l = 'ru';
      }
      e.value = this.lang_l;
      this.$lang.setLang(this.lang_l)
    }
  }
}
</script>
<style scoped>
a {
  padding-bottom: 15px;
  text-decoration: none;
}

a:hover {
  color: blue;
  text-decoration: underline;
}

.spacing {
  margin-right: 10px;
}

.header-message {
  display: flex;
  align-items: center;
  width: 90%;
  align-items: center;
}

.change-lang {
  display: flex;
  align-items: center;
  width: 10%;
}

.page {
  display: flex;
  flex-direction: column;
}

.header-title {
  display: flex;
  padding: 30px 90px;
  height: 10%;
  border-bottom: 2px solid green
}

.content {
  display: flex;
  flex-direction: row;
}

.main-menu {
  display: flex;
  width: 20%;
  text-align: left;
  flex-direction: column;
  align-items: flex-start;
  padding-left: 20px;
  border-right: 2px solid green;
}

.text-info {
  width: 80%;
  flex: 0 0 auto;
  overflow: hidden;
  min-height: 400px;
  max-height: 400px;
  overflow-y: scroll;
  overflow-x: scroll;
}

.footer {
    height: 10%;
    border-top: 2px solid green
}

.footer p {
  text-align: center;
}

#app {
  @import './reset.css'
  width: 100%;
}

body {
    font-family: Arial, Helvetica, sans-serif;
}
</style>
