<template>
  <div>
    <h1>{{$lang.messages.numberplates_header}}</h1>
    <p>{{$lang.messages.numberplates_body}}</p>
    <div>
      <button v-on:click="onExportNumberPlate">export</button>
      <input id="fileInput" type="file" style="display:none" v-on:input="onImportFile($event)"/>
      <button id="fileInputButton" onclick="document.getElementById('fileInput').click()">Import</button>
    </div>
    <div class="input-data row">
      <div class="medium-1 cell"/>
      <input class="input-cell medium-1 cell"
             type="text" v-model="numberPlate"
             :placeholder="$lang.messages.numberplates_number_plate"/>
      <input class="input-cell medium-3 cell"
             type="text" v-model="description"
             :placeholder="$lang.messages.description"/>
      <label class="input-cell medium-3 cell">
        <input
             type="checkbox" v-model="isWhitelist"/>
        {{$lang.messages.numberplates_checkbox_label}}
      </label>
      <button class="medium-2 cell" @click="add">{{$lang.messages.button_add}}</button>
    </div>
    <table id="table-numberplates">
    <thead>
      <tr>
        <th class="medium-1 cell">№</th>
        <th class="medium-1 cell">{{$lang.messages.numberplates_number_plate}}</th>
        <th class="medium-3 cell">{{$lang.messages.description}}</th>
        <th class="medium-3 cell">{{$lang.messages.numberplates_checkbox_label}}</th>
        <th class="medium-2 cell">{{$lang.messages.action}}</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(item, index) in numberplates" v-bind:key="index" >
        <td class="medium-1">
          {{index + 1}}</td>
        <td class="medium-3">
          <input type="text"
                 class="input-cell-0"
                 readonly="readonly"
                 v-bind:value="item.NumberPlate"
                 /></td>
        <td class="medium-3">
          <input type="text"
                 class="input-cell-0"
                 readonly="readonly"
                 v-bind:value="item.Description"
                 /></td>
        <td class="medium-3">
          <input
             type="checkbox" :checked="isHaveDetector(item)"
             v-on:change="onSetWhitelist(item)"/>
        </td>
        <td class="medium-2">
          <!--<button type="button" v-on:click="editRow($event, item.Source, item.Destination)" class="fa fa-remove delete-button">Edit</button>-->
          <button type="button" @click="remove(index)" class="fa fa-remove delete-button">{{$lang.messages.button_remove}}</button>
        </td>
      </tr>
    </tbody>
  </table>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

function download (args) {
  let filename, link;

  let csv = args.data || null;
  if (csv == null) {
      return;
  }

  filename = args.filename || 'export.json';

  let blob = new Blob([csv], { type: 'text/plain;charset=utf-8;' })
  link = document.createElement('a');
  let url = URL.createObjectURL(blob)
  link.setAttribute('href', url);
  link.setAttribute('download', filename);
  link.setAttribute('target', '_blank');
  link.click();
}

export default {
  name: 'NumberPlates', //this is the name of the component
  data: function () {
    return {
      numberPlate: '',
      description: '',
      isWhitelist: true,
    }
  },
  computed:  {
    ...mapGetters([
      'numberplates', 'detectors', 'whitelist'
    ]),    
  },
  created() {
    let runPromise = [
      this.$store.dispatch('NUMBER_PLATES_REQUEST'),
      this.$store.dispatch('WHITELIST_REQUEST'),
      this.$store.dispatch('DETECTORS_REQUEST')]
    Promise.all(runPromise)
    .catch( (err) => {
      alert(err)
    })
  },

  methods: {
    ...mapActions(
      ['addNumberPlate', 'removeNumberPlate',]
    ),
    addToWhitelist: function(item, isWhitelist) {
      if ( isWhitelist !== true ) {
          return
        }
        let ids = this.detectors;
        // eslint-disable-next-line
        console.log(ids)
        let items = new Array(0);
        let nameCameras = new Array(0)
        //console.log(ids.flatMap(currVal => {return{ID: 1, IDDetector: currVal.ID, IDNumberPlate: item.ID}}))
        for (let id of ids) {
          items.push({ID: 1, IDDetector: id.ID, IDNumberPlate: item.ID})
          nameCameras.push(id.Name)
        }
        
        // eslint-disable-next-line
        console.log(items)
        this.$store.commit('addItem',
                          {NameCamera: nameCameras, NumberPlate: item.NumberPlate, Description: item.Description, Items: items})
        // eslint-disable-next-line
        console.log(this.whitelist);
    },
    add: function() {
      // eslint-disable-next-line
      console.log("this=", this);
      let item = {
        ID: 1,
        NumberPlate: this.numberPlate,
        Description: this.description,
      };
      // eslint-disable-next-line
      console.log("this=", this);
      this.$store.dispatch('addNumberPlate', item)
      .then( function(resp) {
        this.addToWhitelist(resp, this.isWhitelist)
      }.bind(this))
      .catch( (err) => {
        // eslint-disable-next-line
        console.log(err)
        alert('Number plate add fail')
      })
    },
    remove: function(row){
      // eslint-disable-next-line
      console.log(row);
      this.$store.dispatch('removeNumberPlate', {index: row})
      .then( () => {
        let runPromise = [
          this.$store.dispatch('NUMBER_PLATES_REQUEST'),
          this.$store.dispatch('WHITELIST_REQUEST'),
          this.$store.dispatch('DETECTORS_REQUEST')]
        Promise.all(runPromise)
        .catch( (err) => {
          alert(err)
        })
      })
    },
    isHaveDetector: function(item) {
      return this.whitelist.some((curVal)=>{
        if ( item.NumberPlate == curVal.NumberPlate) {
          return curVal.NameCamera || curVal.NameCamera.length > 0
        }
      })
    },
    onSetWhitelist: function(numberPlate) {
      let whitelist_items = []
      let nameCamera = []
      for (let detector of this.detectors) {
        whitelist_items.push({ID:1, IDDetector: detector.ID, IDNumberPlate: numberPlate.ID})
        nameCamera.push(detector.Name)
      }
      let methodAction = ''
      if (!this.isHaveDetector(numberPlate)) {
        methodAction = 'addItem'
      } else {
        methodAction = 'removeItem'
      }
      this.$store.commit(methodAction,
            {NameCamera: nameCamera, NumberPlate: numberPlate.NumberPlate, Description: numberPlate.Description, Items: whitelist_items})
    },
    onExportNumberPlate() {
      let data = this.numberplates || null;
      if (data == null || !data.length) {
        let msg = 'no data to export';
        // eslint-disable-next-line
        console.log(msg)
        alert(msg);
        return;
      }
      // eslint-disable-next-line
      console.log(data)
      download({data: JSON.stringify(data)});
    },

    onImportFile(e) {
      const file = e.currentTarget.files[0];
      const reader = new FileReader();

      reader.onload = e => {
        let items = JSON.parse(e.target.result)
        
        this.$store.dispatch('replaceNumberPlates', items)
        .then( function(resp) {
          resp.forEach(curVal => {
            let isWhitelist = true
            this.addToWhitelist(curVal, isWhitelist)
          })
        }.bind(this))
        .catch( (err) => {
          // eslint-disable-next-line
          console.log(err)
          alert('Number plate import fail')
        })
        this.items = items;
        // eslint-disable-next-line
        console.log(this.items)
      }
      reader.readAsText(file);
    },
  },
  
    
}

</script>
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

<style scoped>
button:focus, input:focus {
    outline: none;
}

.pages {
  display: flex;
  flex-direction: column;
}
.row {
  margin-top: 10px;
  margin-bottom: 10px;
}

.cell{
  margin: 5px;
  padding: 5px;

}
.input-data {
  display: flex;
  flex-direction: row;
}

.input-cell {
  border-top: 0px;
  border-left: 0px;
  border-right: 0px;
  box-shadow: none;
  background-color: rgba(255,255,255,.30);
  border-color: var(--h2o);
}

.input-cell-0 {
  border: 0px;
  box-shadow: none;
  background-color: rgba(255,255,255,0);
  border-color: var(--h2o);
  padding: 0;
  margin: 0;
  width: 100%;
  text-align: center;
}

.top-titles {
  text-align: center;
  color: var(--green);
  text-transform: uppercase;
  font-size: 75%;
  display: flex;
  flex-direction: row;
}

.table-numberplates {
  display: flex;
  flex-direction: row;
}

.medium-1 {
  width: 8.33333%;
}

.medium-2 {
  width: 12.66667%;
}
.medium-3 {
  width: auto;
}

#table-numberplates {
    font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
}

#table-numberplates td, #table-numberplates th {
    border: 1px solid #ddd;
    padding: 8px;
}

#table-numberplates tr:nth-child(even){background-color: #f2f2f2;}

#table-numberplates tr:hover {background-color: #ddd;}

#table-numberplates th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    background-color: #4CAF50;
    color: white;
}
</style>