import { LISTEVENTS_REQUEST, LISTEVENTS_ERROR, LISTEVENTS_SUCCESS } from '../actions/listevents'
import axios from 'axios';

const state = { listevents: [] }

const getters = {
  listevents: state => state.listevents,
}

const actions = {
  [LISTEVENTS_REQUEST]: async (context) => {
      let {data} = await axios
      .get('/softwarecamera/1.0.0/listevents')
      context.commit(LISTEVENTS_SUCCESS, data)
  },
}

const mutations = {
  [LISTEVENTS_SUCCESS]: (state, resp) => {
    state.listevents = resp;
  },
  [LISTEVENTS_ERROR]: (error) => {
    if (error.response.status === 401) {
      this.$router.push('/login')
    }
    // eslint-disable-next-line
    console.log(error);
  },
}

export default {
  state,
  getters,
  actions,
  mutations,
}
