import Vue from 'vue'
import Router from 'vue-router'

import Info from '@/components/info'
import Detectors from '@/components/detectors'
import NumberPlates from '@/components/numberplates'
import ChangeAuth from '@/components/auth/change'
import Login from '@/components/auth/login'
import SetupEvents from '@/components/events/setup'
import ListEvents from '@/components/events/list'
import Calibration from '@/components/calibration'
import Onvif from '@/components/onvif'
import store from '@/store'

Vue.use(Router)

const ifNotAuthenticated = (to, from, next) => {
  if (!store.getters.isAuthenticated) {
    next()
    return
  }
  next('/')
}

const ifAuthenticated = (to, from, next) => {
  if ( store.getters.isAuthenticated ) {
    next()
    return
  }
  next('/login')
}

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Info',
      component: Info,
      beforeEnter: ifAuthenticated,
    },
    {
      path: '/info',
      name: 'Info',
      component: Info,
      beforeEnter: ifAuthenticated,
    },
    {
      path: '/detectors',
      name: 'Detectors',
      component: Detectors,
      beforeEnter: ifAuthenticated,
    },
    {
      path: '/numberplates',
      name: 'NumberPlates',
      component: NumberPlates,
      beforeEnter: ifAuthenticated,
    },
    {
      path: '/auth',
      name: 'ChangeAuth',
      component: ChangeAuth,
      beforeEnter: ifAuthenticated,
    },
    {
      path: '/setupevents',
      name: 'Setup events display',
      component: SetupEvents,
      beforeEnter: ifAuthenticated,
    },
    {
      path: '/listevents',
      name: 'List events display',
      component: ListEvents,
      beforeEnter: ifAuthenticated,
    },
    {
      path: '/calibration',
      name: 'Direction calibration',
      component: Calibration,
      beforeEnter: ifAuthenticated,
    },
    {
      path: '/onvif',
      name: 'Setup onvif',
      component: Onvif,
      beforeEnter: ifAuthenticated,
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
      beforeEnter: ifNotAuthenticated,
    },
  ]
})