<template>
  <div>
    <h1>{{$lang.messages.onvif_header}}</h1>
    <p>{{$lang.messages.onvif_body}}</p>
    <button v-on:click="restartButton">{{$lang.messages.onvif_button_restart}}</button>
  </div>
</template>

<script>
export default {
  name: 'Onvif', //this is the name of the component
  methods: {
    restartButton: function () {
      this.$store.dispatch('ONVIF_RESTART_REQUEST')
      .catch( () => {
        alert('Onvif server restart fail')
      })
    },
  }

}
</script>
