<template>
  <div>
   <form class="login" @submit.prevent="login">
     <h1>{{$lang.messages.auth_header}}</h1>
     <label>{{$lang.messages.auth_user_name}}</label>
     <input required v-model="username" type="text" :placeholder="$lang.messages.auth_placeholder_login"/>
     <label>{{$lang.messages.auth_password}}</label>
     <input required v-model="password" type="password" :placeholder="$lang.messages.auth_placeholder_password"/>
     <hr/>
     <button type="submit">{{$lang.messages.auth_button}}</button>
   </form>
 </div>
</template>

<script>

export default {
  name: 'Login',
  methods: {
      login: function () {
        let username = this.username;
        let password = this.password;
        this.$store.dispatch('AUTH_REQUEST', {user: username, pass: password})
        .then(() => {
          this.$router.push('/')
        })
      },
  }
}
</script>

<style scoped>
  .login {
    display: flex;
    flex-direction: column;
    width: 300px;
    padding: 10px;
  }
</style>

