/* eslint-disable promise/param-names */
import { AUTH_REQUEST, AUTH_ERROR, AUTH_SUCCESS, AUTH_LOGOUT } from '../actions/auth'
import axios from 'axios'

const state = { token: '', status: '', hasLoadedOnce: false }

const getters = {
  isAuthenticated: state => !!state.token,
  authStatus: state => state.status,
}

const actions = {
  [AUTH_REQUEST]: ({commit, dispatch}, payload) => {
    return new Promise((resolve, reject) => {
      commit(AUTH_REQUEST)
      axios
      .post('/softwarecamera/1.0.0/login', {
        auth: {
          username: payload.user,
          password: payload.pass
        }
      })
      .then(resp => {
        commit(AUTH_SUCCESS, resp)
        resolve(resp)
      })
      .catch(err => {
        commit(AUTH_ERROR, err)
        // eslint-disable-next-line
        console.log(err)
        reject(err)
      })
    })
  },
  [AUTH_LOGOUT]: ({commit, dispatch}) => {
    return new Promise((resolve, reject) => {
      commit(AUTH_LOGOUT)
      resolve()
    })
  },
}

const mutations = {
  [AUTH_REQUEST]: (state) => {
    state.status = 'loading'
  },
  [AUTH_SUCCESS]: (state, resp) => {
    state.status = 'success'
    axios.defaults.headers.common['user-token'] = resp.data
    state.token = resp.data
    state.hasLoadedOnce = true
  },
  [AUTH_ERROR]: (state) => {
    state.status = 'error'
    axios.defaults.headers.common['user-token'] = ''
    state.token = ''
    state.hasLoadedOnce = true
  },
  [AUTH_LOGOUT]: (state) => {
    state.token = ''
    axios.defaults.headers.common['user-token'] = ''
  }
}

export default {
  state,
  getters,
  actions,
  mutations,
}
