<template>

  <div>
    
    <h1>{{$lang.messages.list_events_header}}</h1>
    <p>{{$lang.messages.list_events_body}}</p>
    <div>
      <table>
        <thead>
          <tr>
            <th v-for="key in fields" v-bind:key="key"
              >
              {{ key.view  }}
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="entry in listevents" v-bind:key="entry">
            <td v-for="key in fields" v-bind:key="key">
              {{entry[key.key]}}
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'ListEvents', //this is the name of the component
  computed:{
    ...mapGetters([
      'listevents',
    ]),
    fields: function(){
      return [{key: 'ID', view: '№'},
              {key: 'NumberPlate', view: this.$lang.messages.list_events_number_plate},
              {key: 'Time', view: this.$lang.messages.list_events_time},
              {key: 'NameCamera', view: this.$lang.messages.list_events_name_camera},
              {key: 'Direction', view: this.$lang.messages.list_events_direction }]
    },
  },
  created() {
    this.$store.dispatch('LISTEVENTS_REQUEST')
    .catch( (err) => {
        alert(err)
    })
  },
}

</script>
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

<style scoped>
.view-grid {
  display: grid;
  margin: 5px;
  border-radius: 5px;
  border: 2px solid;
  text-align: left;
  grid-gap: 1px;
  grid-template-columns: repeat(5, 1fr);
}

.no-space-break {
  white-space:nowrap;
}

label {
  border: 1px solid rgb(0, 0, 0)
}

body {
  font-family: Helvetica Neue, Arial, sans-serif;
  font-size: 14px;
  color: #444;
}

table {
  border: 2px solid #42b983;
  border-radius: 3px;
  background-color: #fff;
}

th {
  background-color: #42b983;
  color: rgba(255,255,255,0.66);
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

td {
  background-color: #DFDFDF;
}

th, td {
  min-width: 120px;
  padding: 10px 20px;
}

th.active {
  color: #fff;
}

th.active .arrow {
  opacity: 1;
}

.arrow {
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 0;
  margin-left: 5px;
  opacity: 0.66;
}

.arrow.asc {
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-bottom: 4px solid #fff;
}

.arrow.dsc {
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 4px solid #fff;
}

</style>