import { DETECTORS_REQUEST, DETECTORS_ERROR, DETECTORS_SUCCESS, DETECTOR_DELETE, DETECTOR_ADD } from '../actions/detectors'
import Vue from 'vue'
import axios from 'axios';

const state = { detectors: [] }

const getters = {
  detectors: state => state.detectors,
}

const actions = {
  [DETECTORS_REQUEST]: async (context) => {
      let {data} = await axios
      .get('/softwarecamera/1.0.0/detectors')
      context.commit(DETECTORS_SUCCESS, data)
  },
  addDetector: async (context, payload) => {
    let {data} = await axios
    .post('/softwarecamera/1.0.0/detector', payload)

    payload.ID = data.LastInsertId
    context.commit(DETECTOR_ADD, payload)
  },
  removeDetector: async (context, payload) => {
    await axios
    .delete('/softwarecamera/1.0.0/detector',  {data: state.detectors[payload.index]})
    context.commit(DETECTOR_DELETE, payload.index)
  },
  changeDetector (state, payload) {
    payload.item[payload.field] = payload.val;
    Vue.set(state.detectors, payload.index, payload.item)
  },
}

const mutations = {
  [DETECTORS_SUCCESS]: (state, resp) => {
    state.detectors = resp;
  },
  [DETECTORS_ERROR]: (error) => {
    if (error.response.status === 401) {
      this.$router.push('/login')
    }
    // eslint-disable-next-line
    console.log(error);
  },
  [DETECTOR_ADD]: (state, payload) => {
    state.detectors.push(payload);
  },
  [DETECTOR_DELETE]: (state, index) => {
    state.detectors.splice(index, 1);
  }
}

export default {
  state,
  getters,
  actions,
  mutations,
}
