import { ONVIF_RESTART_REQUEST, ONVIF_ERROR, ONVIF_SUCCESS } from '../actions/onvif'
import axios from 'axios';

const state = { onvif: [] }

const getters = {
  onvif: state => state.onvif,
}

const actions = {
  [ONVIF_RESTART_REQUEST]: ({commit, dispatch}) => {
    return new Promise((resolve, reject) => {
      axios
      .post('/softwarecamera/1.0.0/onvif')
      .then(resp => {
        commit(ONVIF_SUCCESS, resp)
        resolve(resp)
      })
      .catch(error => {
        dispatch(ONVIF_ERROR, error)
        reject(error)
      });
    })
  },
}

const mutations = {
  [ONVIF_SUCCESS]: (state, resp) => {
    state.onvif = resp.data;
  },
  [ONVIF_ERROR]: (error) => {
    if (error.response.status === 401) {
      this.$router.push('/login')
    }
    // eslint-disable-next-line
    console.log(error);
  },
}

export default {
  state,
  getters,
  actions,
  mutations,
}
