<template>
  <div>
    <h1>{{$lang.messages.detectors_header}}</h1>
    <p>{{$lang.messages.detectors_body}}</p>
    <div class="input-data row">
      <div class="medium-1 cell"/>
      <input class="input-cell medium-2 cell"
             type="text" v-model="nameCamera"
             :placeholder="$lang.messages.detectors_name_camera"/>
      <input class="input-cell medium-3 cell"
             type="text" v-model="sourceCamera"
             :placeholder="$lang.messages.detectors_source_camera"/>
      <input class="input-cell medium-3 cell"
             type="text" v-model="destCamera"
             :placeholder="$lang.messages.detectors_dest_camera"/>
      <input class="input-cell medium-2 cell"
             type="text" v-model="serialNumber"
             :placeholder="$lang.messages.detectors_serial_number"/>
      <input class="input-cell medium-2 cell"
             type="text" v-model="serverPort"
             :placeholder="$lang.messages.detectors_server_port"/>
      <button class="medium-2 cell" @click="add">{{$lang.messages.button_add}}</button>
    </div>
    <table id="table-detectors">
    <thead>
      <tr>
        <th class="medium-1 cell">№</th>
        <th class="medium-1 cell">{{$lang.messages.detectors_name_camera}}</th>
        <th class="medium-3 cell">{{$lang.messages.detectors_source_camera}}</th>
        <th class="medium-3 cell">{{$lang.messages.detectors_dest_camera}}</th>
        <th class="medium-3 cell">{{$lang.messages.detectors_serial_number}}</th>
        <th class="medium-3 cell">{{$lang.messages.detectors_server_port}}</th>
        <th class="medium-2 cell">{{$lang.messages.action}}</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(item, index) in detectors" v-bind:key="index" >
        <td class="medium-1">
          {{index + 1}}</td>
        <td class="medium-3">
          <input type="text"
                 class="input-cell-0"
                 v-bind:value="item.Name"
                 @change="onChangeItem($event.target.value, item, 'Name', index)"/></td>
        <td class="medium-3">
          <input type="text"
                 class="input-cell-0"
                 v-bind:value="item.Source"
                 @change="onChangeItem($event.target.value, item, 'Source', index)"/></td>
        <td class="medium-3">
          <input type="text"
                 class="input-cell-0"
                 v-bind:value="item.Destination"
                 @change="onChangeItem($event.target.value, item, 'Destination', index)"/></td>
        <td class="medium-3">
          <input type="text"
                 class="input-cell-0"
                 v-bind:value="item.SerialNumber"
                 @change="onChangeItem($event.target.value, item, 'SerialNumber', index)"/></td>
        <td class="medium-3">
          <input type="number"
                 class="input-cell-0"
                 v-bind:value="item.ServerPort"
                 @change="onChangeItem(+$event.target.value, item, 'ServerPort', index)"/></td>
        <td class="medium-2">
          <!--<button type="button" v-on:click="editRow($event, item.Source, item.Destination)" class="fa fa-remove delete-button">Edit</button>-->
          <button type="button" @click="remove(index)" class="fa fa-remove delete-button">{{$lang.messages.button_remove}}</button>
        </td>
      </tr>
    </tbody>
  </table>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  name: 'Detectors', //this is the name of the component
  data: function () {
    return {
      nameCamera: '',
      sourceCamera: '',
      destCamera: '',
      serialNumber: '',
      serverPort: 0,
    }
  },
  computed:  {
    ...mapGetters([
      'detectors',
    ]),
  },
  created() {
    this.$store.dispatch('DETECTORS_REQUEST')
    .catch( (err) => alert(err))
  },

  methods: {
    ...mapActions([
      'addDetector', 'removeDetector', 'changeDetector'
    ]),
    add() {
      // eslint-disable-next-line
      console.log("source=", this.sourceCamera, "dest=", this.destCamera);
      var item = {
        ID: 1,
        Name: this.nameCamera,
        Source: this.sourceCamera,
        Destination: this.destCamera,
        SerialNumber: this.serialNumber,
        ServerPort: +this.serverPort,
      };
      this.$store.dispatch('addDetector', item)
      // eslint-disable-next-line
      console.log(this.detectors);
    },
    
    remove: function(row){
      // eslint-disable-next-line
      console.log(row);
      this.$store.dispatch('removeDetector', {index: row})
      .then( () => {
        let runPromise = [
          this.$store.dispatch('NUMBER_PLATES_REQUEST'),
          this.$store.dispatch('WHITELIST_REQUEST'),
          this.$store.dispatch('DETECTORS_REQUEST')]
        Promise.all(runPromise)
        .catch( (err) => {
          alert(err)
        })
      })
    },
    onChangeItem: function(val, item, field, index) {
      this.$store.dispatch('changeDetector', {val: val, item: item, field: field, index: index})
    }
  },
}

</script>
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

<style scoped>
button:focus, input:focus {
    outline: none;
}

.pages {
  display: flex;
  flex-direction: column;
}
.row {
  margin-top: 10px;
  margin-bottom: 10px;
}

.cell{
  margin: 5px;
  padding: 5px;

}
.input-data {
  display: flex;
  flex-direction: row;
}

.input-cell {
  border-top: 0px;
  border-left: 0px;
  border-right: 0px;
  box-shadow: none;
  background-color: rgba(255,255,255,.30);
  border-color: var(--h2o);
}

.input-cell-0 {
  border: 0px;
  box-shadow: none;
  background-color: rgba(255,255,255,0);
  border-color: var(--h2o);
  padding: 0;
  margin: 0;
  width: 100%;
  text-align: center;
}

.top-titles {
  text-align: center;
  color: var(--green);
  text-transform: uppercase;
  font-size: 75%;
  display: flex;
  flex-direction: row;
}

.table-detectors {
  display: flex;
  flex-direction: row;
}

.medium-1 {
  width: 8.33333%;
}

.medium-2 {
  width: 12.66667%;
}
.medium-3 {
  width: 40%;
}

#table-detectors {
    font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
}

#table-detectors td, #table-detectors th {
    border: 1px solid #ddd;
    padding: 8px;
}

#table-detectors tr:nth-child(even){background-color: #f2f2f2;}

#table-detectors tr:hover {background-color: #ddd;}

#table-detectors th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    background-color: #4CAF50;
    color: white;
}
</style>