import { WHITELIST_REQUEST, WHITELIST_ERROR, WHITELIST_SUCCESS } from '../actions/whitelist'
import Vue from 'vue'
import axios from 'axios';

const state = { whitelist: [] }

const getters = {
  whitelist: state => state.whitelist,
}

const actions = {
  [WHITELIST_REQUEST]: ({commit, dispatch}) => {
    return new Promise((resolve, reject) => {
      axios
      .get('/softwarecamera/1.0.0/whitelist')
      .then(resp => {
        commit(WHITELIST_SUCCESS, resp)
        resolve(resp)
      })
      .catch(error => {
        dispatch(WHITELIST_ERROR, error)
        reject(error)
      });
    })
  }
}

const mutations = {
  addItem (state, payload) {
      axios
      .post('/softwarecamera/1.0.0/whitelistitem', payload.Items)
      .then( () => {
        state.whitelist.push(payload);
      })
  },
  removeItem (state, payload) {
    axios
    .delete('/softwarecamera/1.0.0/whitelistitem', {data: payload.Items})
    .then( () => {
      var idx = state.whitelist.findIndex(function(element){
        return element == payload
      })
      if (idx != -1) {
        state.whitelist.splice(idx, 1);
      }
    })
  },
  changeItem (state, payload) {
    payload.item[payload.field] = payload.val;
    Vue.set(state.whitelist, payload.index, payload.item)
  },
  [WHITELIST_SUCCESS]: (state, resp) => {
    state.whitelist = resp.data;
  },
  [WHITELIST_ERROR]: (error) => {
    if (error.response.status === 401) {
      this.$router.push('/login')
    }
    // eslint-disable-next-line
    console.log(error);
  }
}

export default {
  state,
  getters,
  actions,
  mutations,
}
