import { SETUPEVENTS_REQUEST, SETUPEVENTS_ERROR, SETUPEVENTS_SUCCESS } from '../actions/setupevents'
import axios from 'axios';

const state = { setupevents: [] }

const getters = {
  setupevents: state => state.setupevents,
}

const actions = {
  [SETUPEVENTS_REQUEST]: ({commit, dispatch}) => {
    return new Promise((resolve, reject) => {
      axios
      .get('/softwarecamera/1.0.0/setupevents')
      .then(resp => {
        commit(SETUPEVENTS_SUCCESS, resp)
        resolve(resp)
      })
      .catch(error => {
        dispatch(SETUPEVENTS_ERROR, error)
        reject(error)
      });
    })
  },
  changeEvent (state, payload) {
    return new Promise((resolve, reject) => {
      // eslint-disable-next-line
      console.log(payload)
      axios
      .post('/softwarecamera/1.0.0/setupevents', payload)
      .then( resp => {
        resolve(resp)
      })
      .catch(error => {
        reject(error)
      });
    })
  },
}

const mutations = {
  [SETUPEVENTS_SUCCESS]: (state, resp) => {
    state.setupevents = resp.data;
  },
  [SETUPEVENTS_ERROR]: (error) => {
    if (error.response.status === 401) {
      this.$router.push('/login')
    }
    // eslint-disable-next-line
    console.log(error);
  },
}

export default {
  state,
  getters,
  actions,
  mutations,
}
