<template>
  <div>
    <h1>{{$lang.messages.calibration_header}}</h1>
    <div class="view-grid">
          <label for="drive-off">{{$lang.messages.calibration_drive_off}}:</label>
          <select v-model="selectedDriveOff" @change="changeParamDirection">
            <option v-for="(item) in optionsDirection"  v-bind:value="item" v-bind:key="item">{{item.text}}</option>
          </select>

          <label for="drive-up">{{$lang.messages.calibration_drive_up}}:</label>
          <select v-model="selectedDriveUp" @change="changeParamDirection">
            <option v-for="(item) in optionsDirection"  v-bind:value="item" v-bind:key="item">{{item.text}}</option>
          </select>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex';

export default {
  name: 'Calibration', //this is the name of the component
  data: function () {
    return {
      selectedDriveOff: -1,
      selectedDriveUp:  -1,
    }
  },
  computed:  {
    ...mapGetters([
      'calibration',
    ]),
    optionsDirection: function() {
      return  [
        {text: this.$lang.messages.calibration_drive_to, value: 1},
        {text: this.$lang.messages.calibration_drive_out, value: -1},
      ]
    }
  },
  created() {
    this.$store.dispatch('CALIBRATION_REQUEST').then(() => {
      let items = this.calibration;
      for ( var item of items ) {
        if ( item.NameDirection === this.$lang.messages.calibration_drive_off) {
          if ( item.TypeDirection === 1) {
            this.selectedDriveOff = this.optionsDirection[0]
          } else {
            this.selectedDriveOff = this.optionsDirection[1]
          }
        }

        if ( item.NameDirection === this.$lang.messages.calibration_drive_up) {
          if ( item.TypeDirection === 1) {
            this.selectedDriveUp = this.optionsDirection[0]
          } else {
            this.selectedDriveUp = this.optionsDirection[1]
          }
        }
      }
    })
  },
  methods: {
    ...mapMutations([
      'changeDirection'
    ]),
    changeParamDirection() {
      var payload = [
        { NameDirection: 'Drive off', TypeDirection: this.selectedDriveOff.value},
        { NameDirection: 'Drive up' , TypeDirection: this.selectedDriveUp.value },
        ]
      this.$store.dispatch('changeDirection', payload)
      .catch( () => {
        alert('Not saved data')
      })
    }
  },
}
</script>
<style  scoped>
.view-grid {
  width: 30%;
  text-align: left;
  display: grid;
  grid-gap: 12px;
  grid-template-columns: repeat(2, 1fr);
}
</style>