import { NUMBER_PLATES_REQUEST, NUMBER_PLATE_ADD_SUCCESS, NUMBER_PLATES_ERROR, NUMBER_PLATES_SUCCESS, NUMBER_PLATE_REMOVE } from '../actions/numberplates'
import axios from 'axios';

const state = { numberplates: [] }

const getters = {
  numberplates: state => state.numberplates,
}

const actions = {
  [NUMBER_PLATES_REQUEST]: async(context) => {
    let {data} = await axios
    .get('/softwarecamera/1.0.0/numberplates')
    context.commit(NUMBER_PLATES_SUCCESS, data)
  },
  addNumberPlate: ({commit}, payload) => {
    return axios
    .post('/softwarecamera/1.0.0/numberplate', payload)
    .then( (resp) => {
      payload.ID = resp.data.LastInsertId
      commit(NUMBER_PLATE_ADD_SUCCESS, payload)
      return payload
    }) 
  },
  removeNumberPlate: async (context, payload) => {
    await axios
    .delete('/softwarecamera/1.0.0/numberplate', {data: state.numberplates[payload.index]})
    context.commit(NUMBER_PLATE_REMOVE, payload.index)
  },
  
  replaceNumberPlates: ({commit}, payload) => {
    return axios
    .patch('/softwarecamera/1.0.0/numberplate', payload)
    .then ( (resp) => {
      commit(NUMBER_PLATES_SUCCESS, resp.data)
      return resp.data
    })
  }
}

const mutations = {
  [NUMBER_PLATES_SUCCESS]: (state, data) => {
    state.numberplates = data;
  },
  [NUMBER_PLATES_ERROR]: (error) => {
    if (error.response.status === 401) {
      this.$router.push('/login')
    }
    // eslint-disable-next-line
    console.log(error);
  },
  [NUMBER_PLATE_ADD_SUCCESS]: (state, payload) => {
    state.numberplates.push(payload);
  },
  [NUMBER_PLATE_REMOVE]: (state, index) => {
    state.numberplates.splice(index, 1);
  },
}

export default {
  state,
  getters,
  actions,
  mutations,
}
