<template>
  <div>
   <form class="changelogin" @submit.prevent="changelogin">
     <h1>{{$lang.messages.auth_change_header}}</h1>
     <label>{{$lang.messages.auth_change_user_name}}</label>
     <input required v-model="username" type="text" :placeholder="$lang.messages.auth_change_user_name"/>
     <label>{{$lang.messages.auth_change_password}}</label>
     <input required v-model="password" type="password" :placeholder="$lang.messages.auth_change_password"/>
     <label>{{$lang.messages.auth_change_retype_password}}</label>
     <input required v-model="retype_password" type="password" :placeholder="$lang.messages.auth_change_retype_password"/>
     <hr/>
     <button type="submit">{{$lang.messages.auth_change_button}}</button>
   </form>
 </div>
</template>

<script>
//import VueResource from 'vue-resource'
import {AUTH_REQUEST} from '@/store/actions/auth'
export default {
  name: 'ChangeLogin',
  props: {
    msg: String
  },
  methods: {
      changelogin: function () {
        const { username, password } = this
        this.$store.dispatch(AUTH_REQUEST, { username, password }).then(() => {
          this.$router.push('/')
        })
      },
  }
}
</script>

<style scoped>
  .changelogin {
    display: flex;
    flex-direction: column;
    width: 300px;
    padding: 10px;
  }
</style>

